@mixin Border($direction-map) {
  @each $name, $direction in $direction-map {
    $direction: if($direction != 0, "-#{$direction}", null);

    // Additive Border Border-t Border-r Border-b Border-l
    .Border#{$direction} {
      border#{$direction}: 1px solid #dee2e6 !important;
    }

    // Subtractive Border-0 Border-t-0 Border-r-0 Border-b-0 Border-l-0
    .Border#{$direction}-0 {
      border#{$direction}: 0 !important;
    }
  }
}

// Border-width
@mixin Border-Width($max:5) {
  @for $i from 1 through $max {
    .Border-#{$i} {
      border-width: #{$i}px !important;
    }
  }
}

// Border color
@mixin Border-Color($colors) {
  @each $colorKey, $colorValue in $colors {
    .Border-#{to-lower-case($colorKey)} {
      border-color: $colorValue !important;
    }
  }
}