@mixin Background-Dynamic($colors) {
  @each $colorKey, $colorValue in $colors {
    $--bg-Lightness: var(--bg-Lightness, 1);
    $--bg-Alpha: var(--bg-Alpha, 1);
    $--bg-Lighten: var(--bg-Lighten);

    $color-h: #{round(hue($colorValue))};
    $color-s: #{round(saturation($colorValue))}; // 100% def
    $color-l: #{round(lightness($colorValue))}; // 50% def

    // Lightness: 50%; Red Lime Blue Yellow Cyan Magenta Orange Gray // Lighten 50*1.9=95 - Darken 50*.8=40
    $hsla-bg: hsla($color-h, $color-s, calc(#{$color-l} * #{$--bg-Lightness}), #{$--bg-Alpha});

    // TODO: how to condition of dynamic var(--bg-Lightness) value
    @if (lightness($colorValue) < 26%) {
      // Lightness: 25%; Purple Green Maroon Navy Teal Olive Indigo  // Brown Lightness: 38%;  // Pink  Lightness: 63%;
      // Lighten 25*1.9=47.5 - Darken 25*.8=20

      // TODO: condition is not working
      //@if (calc(#{$--bg-Lighten})) {
      //$hsla-bg: hsla($color-h, $color-s, calc(#{$color-l} * #{$--bg-Lightness} * 1.6), #{$--bg-Alpha});
      //}
    }

    //$color-r: #{red($colorValue)};
    //$color-g: #{green($colorValue)};
    //$color-b: #{blue($colorValue)};
    //$rgb-bg: rgba(calc(#{$color-r} * #{$--bg-Lightness}), calc(#{$color-g} * #{$--bg-Lightness}), calc(#{$color-b} * #{$--bg-Lightness}), #{$--bg-Alpha});

    .Bg-#{to-lower-case($colorKey)} {
      --bg-Lightness: 1;
      --bg-Alpha: 1;
      background-color: $hsla-bg !important;
      //background-color: $rgb-bg !important;
    }
  }
}

// .bg-*-l1-l5
@mixin Background-Tint($colors,$rate-values) {
  @each $colorKey, $colorValue in $colors {

    @each $name, $value in $rate-values {
      .Bg-#{to-lower-case($colorKey)}-l#{$name} {
        background-color: Tint($colorValue, $value) !important;
      }
    }
  }
}
//.bg-*-d1-d5
@mixin Background-Shade($colors,$rate-values) {
  @each $colorKey, $colorValue in $colors {

    @each $name, $value in $rate-values {
      .Bg-#{to-lower-case($colorKey)}-d#{$name} {
        background-color: Shade($colorValue, $value) !important;
      }
    }
  }
}

@mixin Opacity() {
  @for $i from 0 through 10 {
    .Opacity-#{$i*10} {
      opacity: #{$i*0.1} !important
    }
  }
}

@mixin Background-Alpha($alpha-values) {
  // ALPHA parameter
  @each $variant in $alpha-values {
    .Bg-alpha-#{$variant} {
      --bg-Alpha: #{$variant*(0.01)};
    }
  }
}

@mixin Background-Lighten($lighten-values) {
  @each $name, $value in $lighten-values {
    .Bg-lighten-#{$name} {
      --bg-Lighten: 1;
      --bg-Lightness: #{$value};
    }
  }
}

@mixin Background-Darken($darken-values) {
  @each $name, $value in $darken-values {
    .Bg-darken-#{$name} {
      --bg-Lightness: #{$value};
    }
  }
}
