@mixin Rounded($infix, $value) {
  $infix: if($infix==null, "", "-#{$infix}");

  .Rounded#{$infix} {
    border-radius: $value !important;
  }

  .Rounded-t#{$infix} {
    border-top-left-radius: $value !important;
    border-top-right-radius: $value !important;
  }
  .Rounded-r#{$infix} {
    border-top-right-radius: $value !important;
    border-bottom-right-radius: $value !important;
  }
  .Rounded-b#{$infix} {
    border-bottom-left-radius: $value !important;
    border-bottom-right-radius: $value !important;
  }
  .Rounded-l#{$infix} {
    border-top-left-radius: $value !important;
    border-bottom-left-radius: $value !important;
  }

  .Rounded-tr#{$infix} {
    border-top-right-radius: $value !important;
  }
  .Rounded-br#{$infix} {
    border-bottom-right-radius: $value !important;
  }
  .Rounded-bl#{$infix} {
    border-bottom-left-radius: $value !important;
  }
  .Rounded-tl#{$infix} {
    border-top-left-radius: $value !important;
  }

  .Rounded-tl-br#{$infix} {
    border-top-left-radius: $value !important;
    border-bottom-right-radius: $value !important;
  }
  .Rounded-bl-tr#{$infix} {
    border-bottom-left-radius: $value !important;
    border-top-right-radius: $value !important;
  }
}
