//::-webkit-scrollbar — the entire scrollbar.
//::-webkit-scrollbar-button — the buttons on the scrollbar (arrows pointing upwards and downwards).
//::-webkit-scrollbar-thumb — the draggable scrolling handle.
//::-webkit-scrollbar-track — the track (progress bar) of the scrollbar.
//::-webkit-scrollbar-track-piece — the part of the track (progress bar) not covered by the handle.
//::-webkit-scrollbar-corner — the bottom corner of the scrollbar, where both horizontal and vertical scrollbars meet.
//::-webkit-resizer — the draggable resizing handle that appears at the bottom corner of some elements.
@mixin Scrollbar() {
  .Scrollbar {
    $foreground-color: #F9FAFB;
    $background-color: lighten($foreground-color, 80%);
    $shadow-inset: //
            0 0 2px 0 rgba(0, 0, 0, .12) inset,
            0 0 8px 0 rgba(0, 0, 0, .09) inset,
            0 0 16px 0 rgba(0, 0, 0, .03) inset;

    $shadow-inset-thumb: //
            0 0 3px 0 rgba(0, 0, 0, .24) inset,
            0 0 9px 0 rgba(0, 0, 0, .12) inset,
            0 0 18px 0 rgba(0, 0, 0, .06) inset;
    $width: .8rem;
    $radius: 0rem;

    // For Google Chrome
    &::-webkit-scrollbar {
      border-radius: 0;
      width: $width;
      height: $width;
    }

    &::-webkit-scrollbar-thumb {
      border-radius: $radius;
      background-clip: padding-box;
      border: 1px solid darken($foreground-color, 6%);
      background: $foreground-color;
      -webkit-box-shadow: $shadow-inset-thumb;
    }

    &::-webkit-scrollbar-button {
      width: 0;
      height: 0;
      display: none;
    }

    &::-webkit-scrollbar-corner {
      background-color: transparent;
    }

    &::-webkit-scrollbar-track {
      border-radius: $radius;
      background: $background-color;
      -webkit-box-shadow: $shadow-inset;
    }

    // For Internet Explorer
    & {
      scrollbar-face-color: $foreground-color;
      scrollbar-track-color: $background-color;
    }
  }
}

// Scrollbar-width
@mixin Scrollbar-Width($max:3) {
  // For Google Chrome
  @each $key, $value in (1:1, 2:1.2, 3:1.4) {
    $width: #{$value}rem;

    .Scrollbar-#{$key} {
      &::-webkit-scrollbar {
        width: $width !important;
        height: $width !important;
      }
    }
  }
}

// Scrollbar border-radius
@mixin Scrollbar-Round() {
  // For Google Chrome
  $radius: .6rem ;

  .Scrollbar-rounded {
    &::-webkit-scrollbar {
      border-radius: $radius;
    }

    &::-webkit-scrollbar-thumb {
      border-radius: $radius;
    }

    &::-webkit-scrollbar-track {
      border-radius: $radius;
    }
  }
}

// Scrollbar color
@mixin Scrollbar-Color($colors) {
  @each $colorKey, $colorValue in $colors {
    .Scrollbar-#{to-lower-case($colorKey)} {
      $foreground-color: $colorValue;
      $background-color: lighten($foreground-color, 80%);

      &::-webkit-scrollbar-thumb {
        border-color: darken($foreground-color, 6%) !important;
        background: $foreground-color !important;
      }

      &::-webkit-scrollbar-track {
        background: $background-color !important;
      }

      // For Internet Explorer
      & {
        scrollbar-face-color: $foreground-color !important;
        scrollbar-track-color: $background-color !important;
      }
    }
  }
}